        copryright_2: " que aplican para hacer Stopover.",
        copryright_term:
          '<div class="web-content-copa common-styles" id="content-wkaq_"><h3>Trminos y condiciones</h3><p>El Stopover permite obtener una (1) parada en Panam sin costo adicional en la tarifa area <strong>nicamente</strong> si se solicita antes de realizar la compra del boleto.</p><p>El pasajero deber pagar los impuestos correspondientes a la entrada y salida de Panam (FZ, AH, F3), aprox. $50. </p><p>El programa Panam Stopover se puede adquirir a travs de los canales de venta de Copa Airlines (Centro de Reservas Copa Airlines, Centro de Servicio ConnectMiles, Oficinas de Ventas y a travs de copa.com <strong>utilizando la opcin de multi-ciudad</strong>). Adicional lo pueden adquirir a travs de Agencias de Viajes. </p><p>El Stopover aplica para: </p><ul><li>Todas las tarifas pblicas, excepto las clases promocionales A y T.  </li><li>Reservas de grupos (+10 pasajeros) que se realizan a travs del Centro de Reservas de Copa Airlines.  </li><li>Boletos emitidos por Copa Airlines que incluyan segmentos de cdigo compartido o interlnea. </li></ul><p>Si el Stopover es solicitado luego de emitido el boleto o iniciado el viaje, no ser gratuito y aplicar el cobro de los cargos adicionales por cambio segn la tarifa adquirida (diferencia de tarifa, penalidades, etc.).  </p><p>Si quieres realizar dos (2) Stopovers, es decir, tanto en la ida como en la vuelta del viaje, debes solicitarlo previo a la emisin del boleto y el segundo Stopover tendr un costo adicional de hasta USD$250.00 ms impuestos.  </p><p>El Stopover se puede solicitar en un itinerario de una va o ida y vuelta.  </p><p>La franquicia de equipaje que aplica es la de Copa Airlines. Aplican cargos por <strong><a href="https://www.copaair.com/es/web/us/politica-equipaje" target="_blank" style="display: inline;">equipaje adicional</a></strong> a la franquicia permitida. Los cargos adicionales se aplicarn nuevamente cuando registres tu equipaje en el Aeropuerto Internacional de Tocumen en Panam luego de tu Stopover. </p><p>Los servicios especiales son solicitados y/o cobrados por segmento excepto cuando es una conexin. Se realiza un primer cobro en el origen y un segundo cobro en Panam.  </p><p>Si el pasajero desea quedarse ms de 7 das, ya no se considera un Stopover y se realiza la reservacin como un multi-ciudad regular sin restriccin de das de estada en Panam aplicando la tarifa que el sistema cotice. </p><p>El pasajero es responsable de todos los requisitos migratorios y de salud para su entrada a Panam: vigencia de pasaporte, visas, tarjetas de turismo, certificado internacional de vacunacin contra la fiebre amarilla y cualquier requisito o documento adicional o especial que se le exija por las disposiciones legales y reglamentarias en vigor. </p><p>&nbsp;</p></div>',
