// @flow
import React, {Component} from 'react';
import {withLocalize} from 'react-localize-redux';

import {connect} from "react-redux";
import {compose} from "redux";

import LanguageToggle from "../LanguageToggle";
import dataPrimaryNav from 'data/PrimaryNav';

import {HashLink as Link} from "react-router-hash-link";


import safetyLogoEs from "../../assets/img/nav/safety_logo_es.svg";
import safetyLogoEn from "../../assets/img/nav/safety_logo_en.svg";


const mapStateToProps = state => {
  return {
    idiomasReducer: state.idiomasReducer
  };
};

class PrimaryNav extends Component {

  constructor(props) {
    super(props);
  }

  safetyLogo(lang) {
    if (lang === "es") {
      return safetyLogoEs;
    } else {
      return safetyLogoEn;
    }
  }

  render() {
    const {currentLanguage} = this.props.idiomasReducer;
    const translations = dataPrimaryNav[currentLanguage];

    const mappingTopNav = (
      <ul className="top-nav-area">
        {/* {
          translations.top_nav.map(
            (item, i) =>
              <li className="top-nav-area-item" key={i}>
                <a
                  id={`key-${i}`}
                  href={`${item.href}`}
                  dangerouslySetInnerHTML={{__html: item.title}}></a>
              </li>
          )
        } */}
        <li className="top-nav-area-item">
          <img
            src={this.safetyLogo(currentLanguage)}
            alt={
              currentLanguage === "es"
                ? "Su seguridad, nuestra prioridad."
                : "Your safety is our priority."
            }
          ></img>
        </li>
        <LanguageToggle/>
      </ul>
    );

    const mappingPrimaryNav = (
      <ul className="primary-nav-area">
        {
          translations.primary_nav.map((item, i) => {
            return (
              <li className="primary-nav-item" key={i}>
                <Link id={`key-${i}`} to={`${item.href}`}>
                  {item.title}
                </Link>
              </li>
            );
          })
        }
      </ul>
    );

    return (
      <div className="row">

        <div className="top-area">

          <div className="logo-header">
            <Link to="/">PANAMA STOPOVER</Link>
          </div>

          <div className="navs-area">

            {mappingTopNav}

            {mappingPrimaryNav}

          </div>

        </div>

      </div>
    );
  }
}

export default compose(withLocalize, connect(mapStateToProps, null))(PrimaryNav);