const idiomasReducer = (state = initialState, action) => {
  const { type } = action;

  switch (type) {
    case 'SET_LANGUAGE':
      return {
        ...state,
        currentLanguage: action.language
      };
    default:
      return state
  }
};

const initialState = {
  currentLanguage: 'es',
  languages: [
    'es',
    'en'
  ]
};

export default idiomasReducer;